import React,{useState,useEffect} from 'react';
import {Container,Typography,makeStyles,Box,Icon,Button,Modal,TextField} from '@material-ui/core'


import './App.css';


const useStyles = makeStyles({
  logo :{
    fontFamily:"Dancing Script",
    fontSize:25,
  },
  container:{
    height:"100vh",
    display:"flex",
    flexDirection:"column",
    justifyContent:"space-between",
    paddingTop:20,
    paddingBottom:20,
    backgroundImage:`url('/images/comingSoon.png')`,
    backgroundPosition:"right",
    backgroundRepeat:"no-repeat",
    ['@media (max-width:780px)']: { // eslint-disable-line no-useless-computed-key
      backgroundImage:`url()`
    }
  },
  heading:{
    fontSize:90,
    fontWeight:"bolder",
    lineHeight:1.2,
    fontFamily:"Poppins",
    ['@media (max-width:780px)']: { // eslint-disable-line no-useless-computed-key
      fontSize:50
    }
  },
  meet:{
    color:"#e50914",
    fontFamily:"Poppins",
    fontWeight:900
  },
  caption:{
      width:"70%",
      marginTop:50,
      fontSize:20,
      ['@media (max-width:780px)']: { // eslint-disable-line no-useless-computed-key
        width:"100%"
      }
  },
  footer:{
    display:"flex",
    justifyContent:"space-between",
    alignItems:"center"
  },
  button:{
    borderRadius:0,
    border:"thin solid white",
    color:"white",
    marginTop:20,
    padding:10,
    paddingLeft:25,
    paddingRight:25
  },
  duration:{
    display:"flex",
    alignItems:"center"
  },
  durationDate:{
    fontSize:30
  },
  durationWord:{
    fontSize:12
  },
  durationBox:{
    height:15,
    width:1,
    backgroundColor:"white",
    marginLeft:10,
    marginRight:10,
    marginTop:-20
  },
  modalContainer:{
    alignItems:"center",
    height:"100%",
    display:"flex",
    "&:active":{
      outline:"none"
    },
    "&:focus":{
      outline:"none",
      border:"none"
    },
    ['@media (max-width:488px)']: { // eslint-disable-line no-useless-computed-key
      flexDirection:"column",
      justifyContent:"center"
    }
  },
  button2:{
    borderRadius:0,
    border:"thin solid white",
    color:"white",
    marginTop:20,
    padding:10,
    paddingLeft:25,
    paddingRight:25,
    ['@media (max-width:488px)']: { // eslint-disable-line no-useless-computed-key
      width:"100%"
    }
  },
  button3:{
    borderRadius:0,
    border:"thin solid white",
    color:"white",
    marginTop:20,
    padding:10,
    paddingLeft:25,
    paddingRight:25,
    width:"100%",
    display:"none",
    ['@media (max-width:488px)']: { // eslint-disable-line no-useless-computed-key
      display:"block"
    }
  },
  textInput: {
    "& .MuiInput-input":{
      color:"white",
      borderBottom:"thin solid white",
      marginRight:10,
      width:300
    },
    "& label.Mui-focused": {
      color: "white"
    },
    "& .MuiInput-underline:after": {
      borderBottomColor: "white"
    },
    "& .MuiFormLabel-root": {
      color: "white"
    },
    "& .MuiInput-underline":{
      marginRight:10
    },
    ['@media (max-width:488px)']: { // eslint-disable-line no-useless-computed-key
      width:"100%"
    }
  },
  modal:{
    background:"rgba(0,0,0,0.7)"
  }

})

function App() {
        const styles = useStyles();
        const [days, setdays] = useState("0");
        const [hours, sethours] = useState("0");
        const [min, setmin] = useState("0");
        const [sec, setsec] = useState("0");
        const [email, setemail] = useState("");
        const [open, setOpen] = useState(false);

        const handleOpen = () => {
          setOpen(true);
        };
      
        const handleClose = () => {
          setOpen(false);
        };

        useEffect(()=>{
          document.title = "Alpha Diva";
        })

       setInterval(function(){
          var date_future = new Date("2020-07-31");
          var date_now = new Date();
  
          var seconds = Math.floor((date_future - (date_now))/1000);
          var minutes = Math.floor(seconds/60);
          var hours = Math.floor(minutes/60);
          var days = Math.floor(hours/24);
          
          hours = hours-(days*24);
          minutes = minutes-(days*24*60)-(hours*60);
          seconds = seconds-(days*24*60*60)-(hours*60*60)-(minutes*60);

          setdays(n(days));
          sethours(n(hours));
          setmin(n(minutes));
          setsec(n(seconds));
        },1000);

        function n(n){
          return n > 9 ? "" + n: "0" + n;
        }

        return (
          <Container maxWidth="md" className={styles.container}>
            <Box>
              <Typography className={styles.logo}>Alpha-Diva</Typography>
            </Box>

            <Box>
              <Typography className={styles.heading}>Nice to <br/><span className={styles.meet}>meet </span> you</Typography>
              <Typography className={styles.caption}>We are preparing something amazing and exciting for you. Special surprise for our subscribers only.</Typography>
              <Button className={styles.button} onClick={handleOpen}>Notify me <Icon style={{marginLeft:20}}>notifications_active</Icon></Button>
            </Box>

            <Box className={styles.footer}>
                  <Box>
                      <Icon className="fa fa-facebook" style={{fontSize:15,marginRight:20}} />
                      <Icon className="fa fa-twitter" style={{fontSize:15}}/>
                  </Box>
                  <Box item>
                      <Box className={styles.duration}>
                          <Box>
        <Typography className={styles.durationDate}>{days}</Typography>
                            <Typography className={styles.durationWord}>DAYS</Typography>
                          </Box>
                          <Box className={styles.durationBox}></Box>

                          <Box>
        <Typography className={styles.durationDate}>{hours}</Typography>
                            <Typography className={styles.durationWord}>HOURS</Typography>
                          </Box>
                          <Box className={styles.durationBox}></Box>

                          <Box>
        <Typography className={styles.durationDate}>{min}</Typography>
                            <Typography className={styles.durationWord}>MIN</Typography>
                          </Box>
                          <Box className={styles.durationBox}></Box>

                          <Box>
        <Typography className={styles.durationDate}>{sec}</Typography>
                            <Typography className={styles.durationWord}>SEC</Typography>
                          </Box>
                      </Box>
                  </Box>
            </Box>

            {/* Notify Modal */}
            <Modal
            open={open}
            onClose={handleClose}
            aria-labelledby="simple-modal-title"
            aria-describedby="simple-modal-description"
            className={styles.modal}
          >
         
            <Container maxWidth="md" className={styles.modalContainer}>
            
               <TextField onChange={(e)=>{setemail(e.target.value)}} className={styles.textInput} id="standard-basic" label="Email" />
               <Button className={styles.button2} onClick={()=>{
                 if(email.trim().length > 0){
                   handleClose()
                 }
               }}>Submit</Button>

        <Button className={styles.button3} onClick={()=>{
                  handleClose()
               }}>Cancel</Button>
           
            </Container>
          </Modal>

          </Container>
    );
  }
 

export default App;
